<template>
<div>
  <!--  -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <!--  -->
  <el-card class="box-card">
<!--    -->
    <el-input
      placeholder=""
      class="query-input-style"
      v-model.trim="queryOrderInfo.query"
      clearable
      @clear="getOrderData"
    >
      <el-button slot="append" icon="el-icon-search"
                 @click="getOrderData"
      ></el-button>
    </el-input>
<!--    -->
    <el-table
      :data="orderData"
      stripe
      border
      style="width: 100%"
    >
<!--      -->
      <el-table-column
        label="#"
        type="index"
        :index="computedIndex"
      ></el-table-column>
<!--      -->
      <el-table-column
        label=""
        prop="order_number"
        width="500px"
      ></el-table-column>
<!--      -->
      <el-table-column
        label=""
        prop="order_price"
      ></el-table-column>
<!--      -->
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.pay_status*1===0"></el-tag>
          <el-tag type="success" v-if="scope.row.pay_status*1!==0"></el-tag>
        </template>
      </el-table-column>
<!--      -->
      <el-table-column
        label=""
        prop="is_send"
      ></el-table-column>
<!--      -->
      <el-table-column
        label=""
        width="200"
      >
        <template slot-scope="scope">
          {{ scope.row.create_time | datefmt('YYYY MMM Do YY') }}
        </template>
      </el-table-column>
<!--      -->
      <el-table-column
        label=""
      >
<!--        -->
        <el-button type="primary"
                   class="el-icon-edit"
                   size="mini"
                   @click="editOrderBtn"
        ></el-button>
        <el-button class="el-icon-location-outline"
                   size="mini"
                   type="success"
                   @click="showProgress"
        ></el-button>
      </el-table-column>
    </el-table>
<!--    -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryOrderInfo.pagenum*1"
      :page-sizes="[3, 6, 9, 12]"
      :page-size="queryOrderInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="orderTotal">
    </el-pagination>
  </el-card>
<!--  dialog-->
  <el-dialog
    title=""
    :visible.sync="editDialogVisible"
    width="50%"
    @close="addressDialogClose"
    >
    <el-form :model="addressFormData" status-icon
             :rules="addressFormRules" ref="addressFormRef" label-width="100px">
<!--      -->
      <el-form-item label="/" prop="selectCateKeys">
        <el-cascader
          :options="citydata"
          clearable
          :props="{
                  value:'value',
                  label:'label',
                  children:'children',
                  expandTrigger: 'hover',
                  multiple:false
                  }"
          v-model="addressFormData.selectCateKeys"
        ></el-cascader>
      </el-form-item>
<!--      -->
      <el-form-item label="" prop="detailAddress">
        <el-input v-model="addressFormData.detailAddress"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editDialogVisible = false"> </el-button>
  </span>
  </el-dialog>
<!--  dialog-->
  <el-dialog
    title=""
    :visible.sync="progressDialogVisible"
  >
    
  </el-dialog>
</div>
</template>

<script>
//
import citydata from './citydata'

export default {
  name: 'Order',
  created () {
    this.getOrderData()
  },
  data(){
    return {
      //
      queryOrderInfo: {
        query: '',
        pagenum: '1',
        pagesize: 3
      },
      //
      orderData: [],
      //
      orderTotal: 0,
      //dialog visible
      editDialogVisible: false,
      //dialog   visible
      progressDialogVisible: false,
      //
      citydata,
      //
      addressFormData: {
        selectCateKeys: [], //
        detailAddress: ''   //
      },
      //
      addressFormRules:{
        detailAddress: [
          { required: true, message: '', trigger: 'blur' }
        ],
        selectCateKeys: [
          {required:true, message:'/', trigger: blur}
        ]
      }
    }
  },
  methods:{
    //
    getOrderData(){
      this.$http({
        method:'get',
        url:'orders',
        params:this.queryOrderInfo
      }).then(({data: res})=>{
        if(res.meta.status !== 200) {
          return this.$message.error('')
        }else {
          this.orderData = res.data.goods
          this.orderTotal = res.data.total //
        }
      })
    },
    //
    handleSizeChange(newPageSize){
      this.queryOrderInfo.pagesize = newPageSize
      this.getOrderData()
    },
    //
    handleCurrentChange(newCurrentPage){
      this.queryOrderInfo.pagenum = newCurrentPage
      this.getOrderData()
    },
    //
    editOrderBtn(){
      this.editDialogVisible = true
    },
    //dialog
    addressDialogClose(){
      this.$refs.addressFormRef.resetFields()
    },
    //
    showProgress(){
      this.progressDialogVisible = true
    }
  },
  computed:{
    // 
    computedIndex(){
      let i = 1
      return i++
    }
  }
}
</script>

<style scoped>
.query-input-style {
  width: 40%;
}
.el-cascader {
  width: 100%;
}
</style>
